import React from 'react'
import styled from 'styled-components'
import AlphabetManager from 'AlphabetManager'
import Box from 'components/ui/Box'
import Button from 'components/ui/button'
import LetterBox from 'components/ui/LetterBox'

const manager = new AlphabetManager()

const GameBoard = ({ className }) => {
    const [gameStatus, setGameStatus] = React.useState('ready')
    const [current, setCurrent] = React.useState()
    const [available, setAvailable] = React.useState([])
    const [extracted, setExtracted] = React.useState([])

    const selectAlphabet = (easy) => {
        manager.start(easy)
        setGameStatus('started')
        setAvailable(manager.available)
        setExtracted(manager.extracted)
        setCurrent(undefined)
    }

    const extract = () => {
        try {
            const extractedLetter = manager.extract()
            setCurrent(extractedLetter)
        } catch (e) {
            setGameStatus('finished')
        }
    }

    React.useEffect(() => {
        if (current) {
            setAvailable(manager.available)
            setExtracted(manager.extracted)
        }
    }, [current])

    return (
        <div className={className}>
            {gameStatus === 'ready' && (
                <>
                    <Box
                        title="Seleziona il tipo di alfabeto per iniziare"
                        transparent={false}
                    >
                        <Button onClick={() => selectAlphabet(true)}>
                            Facile
                        </Button>{' '}
                        <Button onClick={() => selectAlphabet(false)}>
                            Difficile
                        </Button>
                    </Box>
                </>
            )}
            {gameStatus === 'started' && (
                <>
                    <Box>
                        <LetterBox>{current}</LetterBox>
                        <Button onClick={extract} type="success">
                            Estrai
                        </Button>
                    </Box>
                    <Box transparent={false} title="Lettere rimaste">
                        {available.map((letter) => (
                            <React.Fragment key={`available_${letter}`}>
                                {letter}{' '}
                            </React.Fragment>
                        ))}
                    </Box>
                    <Box transparent={false} title="Lettere estratte">
                        {extracted.map((letter) => (
                            <React.Fragment key={`available_${letter}`}>
                                {letter}{' '}
                            </React.Fragment>
                        ))}
                    </Box>
                    <Box>
                        <p>
                            <Button
                                onClick={() => setGameStatus('ready')}
                                type="cancel"
                            >
                                Ricomincia
                            </Button>
                        </p>
                    </Box>
                </>
            )}
            {gameStatus === 'finished' && (
                <Box title="Partita finita!" transparent={false}>
                    <Button onClick={() => setGameStatus('ready')}>
                        Ricomincia
                    </Button>
                </Box>
            )}
        </div>
    )
}

export default styled(GameBoard)`
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    & > div {
        margin-top: 20px;
    }
`
