import React from 'react'
import {
    BaseButton,
    CancelButton,
    SuccessButton,
} from 'components/ui/button/styled'

const Button = ({ children, onClick = () => {}, type = 'base' }) => {
    const chooseButton = (type) => {
        switch (type) {
            case 'success':
                return SuccessButton
            case 'cancel':
                return CancelButton
            default:
                return BaseButton
        }
    }

    const renderButton = (Element, children) => {
        return <Element onClick={onClick}>{children}</Element>
    }

    return renderButton(chooseButton(type), children)
}

export default Button
