import React from 'react'
import styled from 'styled-components'

export const BoxStyle = styled.div`
    background-color: ${(props) =>
        props.transparent ? 'transparent' : '#EEE'};
    width: 95%;
    padding: 20px;
    font-size: 1.8rem;
    h4 {
        margin: 0 0 10px 0;
        padding: 0;
        font-size: 2.2rem;
    }
    box-shadow: ${(props) =>
        !props.transparent ? '0 0 8px 0 rgba(106, 107, 96, 0.42)' : 'none'};
`

export const BoxContent = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #333;
`

const Box = ({ children, transparent = true, title }) => {
    return (
        <BoxStyle transparent={transparent}>
            {title && <h4>{title}</h4>}
            <BoxContent>{children}</BoxContent>
        </BoxStyle>
    )
}

export default Box
